import {memo, useCallback, useState} from 'react';
import Animated, {useAnimatedStyle} from 'react-native-reanimated';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import {ImageDTO} from '@/components/CameraRollPicker/useGallery';
import {ScreenWrapper} from '@true/ui';
import {styled} from '@true/core';
import {CameraRollPicker} from '@/components/CameraRollPicker/CameraRollPicker';
import {Fonts, goBack} from '@true/core';
import {DynamicHeader} from '@/components/DynamicHeader';
import {useNavigationParams} from '@true/core';
import {Icons} from '@true/asset';

export interface CameraRollScreenParams {
  onSubmit: (selected: ImageDTO) => void;
}

export const CameraRollScreen = memo(function () {
  const {aTop} = useAnimatedSafeAreaInsets();
  const {onSubmit} = useNavigationParams<CameraRollScreenParams>();

  const [selectedImage, setSelectedImage] = useState<ImageDTO[]>([]);
  // useStatusBarStackEntry(true, '#fff', 'dark-content');

  const animatedStyles = useAnimatedStyle(() => ({
    paddingTop: aTop.value,
  }));

  const _onSelectedImage = useCallback((image: ImageDTO) => {
    onSubmit(image);
    goBack();
  }, []);

  return (
    <ScreenWrapper>
      <SContainer style={animatedStyles}>
        <DynamicHeader
          renderLeftComponent={
            <SIconWrapper onPress={goBack}>
              <SIcon source={Icons.Outlined.X_MARK} />
            </SIconWrapper>
          }
          renderMiddleComponent={<STextHeader>Select a background</STextHeader>}
        />
        <CameraRollPicker
          isBottomSheet={false}
          onSelectImage={_onSelectedImage}
          selected={selectedImage}
          isMarked={false}
        />
      </SContainer>
    </ScreenWrapper>
  );
});
const SContainer = styled(Animated.View)`
  background-color: #fff;
  flex: 1;
`;

const SIconWrapper = styled.TouchableOpacity`
  padding: 16px;
`;
const SIcon = styled.Image`
  width: 24px;
`;
const STextHeader = styled.Text`
  font-family: ${Fonts.Regular};
  font-weight: 700;
  font-size: 20px;
  text-align: center;
  color: ${p => p.theme.neutral1};
`;
export default CameraRollScreen;
